<template>
  <div class="item-todo">
    <div class="list-group-item d-flex justify-content-between align-items-center">
      <div class="user-title" :class="[setMarkStyle, setDoneStyle]">
        <a href="javascript:void(0)"
           @Click="onDoneClick(itemList.id)">
          {{ itemList.userName }}
        </a>
      </div>
      <div class="list-controls">
        <button
            @click="onImportantClick(itemList.id)"
            class="btn btn-outline-primary">
          <img src="../assets/pencil-square.svg" alt="icon">
        </button>
        <button
            @click="onDelete(itemList.id)"
            class="btn btn-outline-primary">
          <img src="../assets/x-lg.svg" alt="icon">
        </button>
      </div>
    </div>
  </div>
</template>

<script>

import {mapActions} from 'vuex';

export default {
  name: 'TodoItem',
  props: {
    itemList: {
      type: Object,
      required: true
    }
  },
  computed: {
    setDoneStyle() {
      return this.itemList.done ? 'done' : ''
    },
    setMarkStyle() {
      return this.itemList.important ? 'important' : ''
    }
  },
  methods: {
    ...mapActions('userStore', ['toggleDone', 'onDeleteList']),
    onDoneClick(id) {
      let prop = {
        id,
        prop: 'done'
      }
      this.toggleDone(prop);
    },

    onImportantClick(id) {
      let prop = {
        id,
        prop: 'important'
      }
      this.toggleDone(prop);
    },

    onDelete(id) {
      this.onDeleteList(id);
    }
  }
}

</script>

<style scoped>
.list-controls button + button {
  margin-left: 10px;
}

.user-title {
  cursor: pointer;
}

.done a {
  text-decoration: line-through;
  color: #000 !important;
}

.important a {
  font-size: 16px;
  font-weight: bolder;
  color: #ff0000;
}
</style>