    <div class="on-search" v-if="getSearchValue">Search result</div>
    <div class="data-list" v-else> {{getDoneItem}} done {{getMoreItem}} more</div>
  </div>
</template>

<script>

import {mapGetters} from 'vuex';

export default {
  name: 'Analytics',
  computed: {
    ...mapGetters('userStore', ['getDoneItem', 'getMoreItem', 'getSearchValue'])
  }
}

</script>

<style scoped>
.analytics-wrapper {
  padding: 7px 20px;
}
</style>