<template>
  <div class="tab-wrapper d-flex">
    <div class="tab-block" v-for="(item, index) in createItem" :key="index">
      <button
          @click="onClick(item.type)"
          class="btn btn-outline-primary"
          :class="[(item.type === setClass) ? 'active' : '']"
      >{{ item.name }}
      </button>
    </div>
  </div>
</template>

<script>

import  {mapActions, mapGetters} from 'vuex';

export default {
  name: 'ActiveTab',
  computed: {
    ...mapGetters('userStore', ['getTabActive']),
    setClass() {
      return this.getTabActive;
    }
  },
  data: () => ({
    createItem: [
      {name: 'All', type: 'all'},
      {name: 'Active', type: 'active'},
      {name: 'Done', type: 'done'}
    ]
  }),
  methods: {
    ...mapActions('userStore', ['toggleTabActive']),

    onClick(prop) {
      this.toggleTabActive(prop);
    },

  },

}
</script>

<style scoped>
.tab-block button {
  outline: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}

.act, .done {
  border-left: none;
}
</style>