<template>
  <div class="card-footer">
    <form class="add-item input-group d-flex" @submit.prevent="onSubmit"  ref="addItemForm">
      <input type="text" class="form-control" placeholder="add Item" ref="inputVal">
      <button type="submit" class="btn btn-outline-primary">
        <img src="../assets/plus-square-fill.svg" alt="icon">
      </button>
    </form>
  </div>
</template>


<script>
import {mapActions} from 'vuex';

export default {
  name: 'AddItem',
  methods: {
    ...mapActions('userStore', ['addUserItem']),

    onSubmit() {
      const value = this.$refs.inputVal.value;

      if (!value) {
        alert('Please fill');
        return;
      }

      this.addUserItem(value);
      this.$refs.addItemForm.reset();
    }
  }

}

</script>

<style>

.add-item button {
  margin-left: 10px;
}

</style>