const userStore = {
    namespaced: true,
    state: {
        toDataList: [
            {userName: 'React + Redux', done: false, important: false, id: 1},
            {userName: 'Vue + Vuex', done: false, important: false, id: 2},
            {userName: 'Angular + Ngx', done: false, important: false, id: 3}
        ],
        searchValue: '',
        tabActive: 'all'
    },
    getters: {
        getDataList: ({toDataList}) => toDataList,

        findIdx: ({toDataList}) => (id) => toDataList.findIndex(item => item.id === id),

        onSearch: ({toDataList, searchValue}) =>
            toDataList.filter(item => item.userName.toLowerCase().indexOf(searchValue.toLowerCase()) > -1),

        getSearchValue: ({searchValue}) => searchValue,

        getTabActive: ({tabActive}) => tabActive,

        tabActive: ({tabActive}, {onSearch}) => {
            switch (tabActive) {
                case 'all':
                    return onSearch
                case 'done':
                    return onSearch.filter(item => item.done === true)
                case 'active':
                    return onSearch.filter(item => item.important === true)
                default:
                    return onSearch
            }
        },

        getDoneItem: ({toDataList}) => toDataList.filter(item => item.done === true).length,

        getMoreItem: ({toDataList}, {getDoneItem}) => toDataList.length - getDoneItem
    },
    mutations: {
        'TOGGLE_PROPERTY'(state, {idx, prop}) {
            const {toDataList} = state;
            toDataList[idx][prop] = !toDataList[idx][prop];
        },

        'DELETE_ITEM_LIST'(state, idx) {
            state.toDataList.splice(idx, 1);
        },

        'ADD_USER_LIST'(state, newUser) {
            state.toDataList.push(newUser);
        },

        'SET_SEARCH_VALUE'(state, val) {
            state.searchValue = val;
        },

        'TOGGLE_TAB_ACTIVE'(state, prop) {
            state.tabActive = prop;
        }

    },
    actions: {
        toggleDone({commit, getters}, {id, prop}) {
            const {findIdx} = getters;
            const idx = findIdx(id)

            commit('TOGGLE_PROPERTY', {idx, prop})
        },

        onDeleteList({commit, getters}, id) {
            const {findIdx} = getters;
            const idx = findIdx(id)

            commit('DELETE_ITEM_LIST', idx);
        },

        addUserItem({commit}, val) {
            let idsRandom = Math.random();
            const newUser = {
                userName: val,
                done: false,
                important: false,
                id: idsRandom
            }

            commit('ADD_USER_LIST', newUser)
        },

        setSearch({commit}, value) {
            commit('SET_SEARCH_VALUE', value);
        },

        toggleTabActive({commit}, prop) {
            commit('TOGGLE_TAB_ACTIVE', prop);
        }

    }
}


export default userStore;

