<template>
  <div class="search-control">
    <input
        v-model="searchVal"
        type="search"
        class="form-control"
        placeholder="Search">
  </div>
</template>

<script>
import {mapActions} from 'vuex';


export default {
  name: 'Search',
  data: () => ({
    searchVal: ''
  }),

  methods: {
    ...mapActions('userStore', ['setSearch']),
    changedSearch(val) {
      this.setSearch(val);
    }
  },

  watch:{
    searchVal: 'changedSearch'
  }
}
</script>